<template>

    <div class="bodyBox">
        <p class="bodyTitle">累计推广收益：{{disProfit}}</p>
        <p class="bodyTitle">{{titleText[navNum]}}用户共{{totalUser}}人</p>
        <ul class="ov levelUser">
            <li class="fl"><a href="javascript:void (0)" @click="navNum = 1" :class="{active:navNum == 1}" class="pr">一级用户6%<span></span></a></li>
            <li class="fl"><a href="javascript:void (0)" @click="navNum = 2" :class="{active:navNum == 2}" class="pr">二级用户5%<span></span></a></li>
            <li class="fl"><a href="javascript:void (0)" @click="navNum = 3" :class="{active:navNum == 3}" class="pr">三级用户4%<span></span></a></li>
        </ul>
        <div class="ov bodyNav">
            <p class="fl">用户</p>
            <p class="fl">微信二维码</p>
            <p class="fl">收益</p>
        </div>
        <userList :currency="currency" :level="1" :navNum="navNum" v-show="navNum == 1" @listenTotal="changeTotal"></userList>
        <userList :currency="currency" :level="2" :navNum="navNum" v-show="navNum == 2" @listenTotal="changeTotal"></userList>
        <userList :currency="currency" :level="3" :navNum="navNum" v-show="navNum == 3" @listenTotal="changeTotal"></userList>
    </div>
</template>

<script>
    import userList from "@/page/mine/red/components/userList"
    import { redDis } from "@/api/user";
    export default {
        name: "Reddis",
        components: {
            userList,
        },
        props: ['currency'],
        data()
        {
            return {
                titleText:['', '一级', '二级', '三级'],
                navNum:1,
                totalMoney:0,
                totalUser:0 ,
                disProfit:0
            }
        },
        mounted()
        {
            this.getDis()
        },
        methods:{

            changeTotal(val)
            {
                this.totalUser = val
            },
            getDis()
            {
                redDis({currency_id:this.currency, page:1}).then(res => {

                    this.disProfit = res.disProfit
                })
            }
        }
    }
</script>

<style scoped>
    .app .bodyBox .bodyTitle {
        height: 0.4rem;
        line-height: 0.4rem;
        background: #F8F8F8;
        font-size: 0.14rem;
        color: #050505;
        text-align: center;
    }
    .app .bodyBox .levelUser {
        background: #F8F8F8;
    }
    .app .bodyBox .levelUser li {
        width: 33.33%;
        text-align: center;
        height: 0.4rem;
        line-height: 0.4rem;
    }
    .app .bodyBox .levelUser li a {
        display: block;
        font-size: 0.13rem;
        color: #515254;
    }
    .app .bodyBox .levelUser li .active span {
        width: 0.3rem;
        height: 0.02rem;
        background: #E33C3D;
        position: absolute;
        bottom: 0;
        left: 50%;
        margin-left: -0.15rem;
    }
    .app .bodyBox .bodyNav {
        background: #f8f8f8;
        height: 0.4rem;
        line-height: 0.4rem;
        font-size: 0.13rem;
        color: #515254;
    }
    .app .bodyBox .bodyNav p {
        width: 33.33%;
        text-align: center;
    }
    .app .bodyBox .conList li {
        height: 0.45rem;
        line-height: 0.45rem;
        border-bottom: 0.01rem solid #f5f5f5;
        font-size: 0.12rem;
        color: #666;
        overflow: hidden;
    }
    .app .bodyBox .conList li span {
        width: 33.33%;
        text-align: center;
        display: inline-block;
        float: left;
    }
    .app .bodyBox .conList li span img {
        margin-top: 0.08rem;
        width: 0.29rem;
        height: 0.29rem;
    }
</style>



