<template>

    <div>
        <ul class="conList">
            <van-list
                    v-model="loading"
                    :finished="finished"
                    finished-text="没有更多了~"
                    @load="getList"
                    v-if="listData.length"
            >
            <li v-for="(item, index) in listData"><span>{{item.nickname ? item.nickname : '未设置昵称'}}</span><span><img :src="item.wechat_code" alt=""></span><span>{{item.disMoney}}</span></li>
            </van-list>
        </ul>
    </div>
</template>

<script>
    import { reduser } from "@/api/user";
    import { List } from "vant";
    import 'vant/lib/list/style';
    export default {
        name: "Reddis",
        components: {
            [List.name]: List,
        },
        props: ['currency', 'level', 'navNum'],
        data()
        {
            return {
                loading:false,
                finished:false,
                page:1,
                listData:[1],
                total:0,
                userCount:0,
            }
        },
        watch:
        {
            navNum(val)
            {

                if (this.navNum  == this.level)
                {
                    this.$emit('listenTotal', this.userCount)
                }

            },

        },
        mounted()
        {
            this.getList()
        },
        methods:{

            // 用户数据
            getList()
            {
                this.loading = true
                reduser({currency_id:this.currency,level:this.level}).then(res => {

                    this.loading = false
                    this.listData = res.list
                    this.total = res.totalMoney
                    this.userCount = res.totalUser
                    this.finished = true
                    if (this.level == 1)
                    {
                        this.$emit('listenTotal', this.userCount)
                    }


                })
            },


        }
    }
</script>

<style scoped>
     .conList li {
        height: 0.45rem;
        line-height: 0.45rem;
        border-bottom: 0.01rem solid #f5f5f5;
        font-size: 0.12rem;
        color: #666;
        overflow: hidden;
    }
    .conList li span {
        width: 33.33%;
        text-align: center;
        display: inline-block;
        float: left;
    }
     .conList li span img {
        margin-top: 0.08rem;
        width: 0.29rem;
        height: 0.29rem;
    }
</style>



