<template>

    <div class="bodyBox">
        <p class="bodyTitle">累计群主收益：{{groupProfit}}</p>
        <div class="ov bodyNav">
            <p class="fl">群名称</p>
            <p class="fl">建群时间</p>
            <p class="fl">收益</p>
        </div>
        <van-list
                v-model="loading"
                :finished="finished"
                finished-text="没有更多了"
                @load="getGroupProfit"
        >
        <ul class="conList">
            <li v-for="item in listData"><span>{{item.name}}</span><span>{{item.create_time}}</span><span>{{item.profit}}</span></li>
        </ul>
        </van-list>
    </div>
</template>

<script>
    import { groupProfit } from "@/api/user";
    import { List } from "vant";
    import 'vant/lib/list/style';
    export default {
        name: "Group",
        components: {
            [List.name]: List,
        },
        props: ['currency'],
        data()
        {
            return {
                loading:false,
                finished:false,
                page:1,
                groupProfit:0,
                listData:[]
            }
        },
        mounted()
        {
            console.log(this.currency)
            this.getGroupProfit()
        },
        methods:{

            // 群主收益
            getGroupProfit()
            {
                this.loading = true
                groupProfit({currency_id:this.currency, page:this.page}).then(res => {

                    this.loading = false
                    if (this.page == 1)
                    {
                        this.groupProfit = res.groupProfit
                    }
                    this.page++
                    this.listData = this.listData.concat(res.list)
                    if ( res.list.length < 20)
                    {
                        this.finished = true
                    }
                })
            }
        }
    }
</script>

<style scoped>
    .app .bodyBox .bodyTitle {
        height: 0.4rem;
        line-height: 0.4rem;
        background: #F8F8F8;
        font-size: 0.14rem;
        color: #050505;
        text-align: center;
        padding: 0 0.15rem;
    }
    .app .bodyBox .bodyNav {
        background: #f8f8f8;
        height: 0.4rem;
        line-height: 0.4rem;
        font-size: 0.13rem;
        color: #515254;
    }
    .app .bodyBox .bodyNav p {
        width: 33.33%;
        text-align: center;
    }
    .app .bodyBox .conList li {
        height: 0.45rem;
        line-height: 0.45rem;
        border-bottom: 0.01rem solid #f5f5f5;
        font-size: 0.12rem;
        color: #666;
        overflow: hidden;
    }
    .app .bodyBox .conList li span {
        width: 33.33%;
        text-align: center;
        display: inline-block;
        float: left;
    }
</style>



