<template>
    <div class="app">
        <!--  公共顶部  -->
        <div class="accountBox">
            <p class="usdLogo"><img :src="currency.icon" alt=""></p>
            <p class="type">{{currency.name}}</p>
            <p class="holder"> <router-link to="/currency?redirect=redindex"><img src="@/static/images/my/qiehuan_red_icon.png" alt=""></router-link></p>
            <div class="holderNum"><span>累计收益</span><img src="@/static/images/my/i_icon.png" alt=""></div>
            <p class="balance">{{redProfit}}</p>
            <ul class="ov titleNav">
                <!--<li class="fl"><a href="javascript:void(0)" class="pr extension" :class="{active:activeNav == 0}" @click="changeNav(0)" >推广收益<span></span></a></li>-->
                <li class="fl"><a href="javascript:void(0)" class="pr group" :class="{active:activeNav == 1}" @click="changeNav(1)">群主收益<span></span></a></li>
                <li class="fl"><a href="javascript:void(0)" class="pr rob" :class="{active:activeNav == 2}" @click="changeNav(2)">抢红包流水<span></span></a></li>
                <li class="fl"><a href="javascript:void(0)" class="pr mySub" :class="{active:activeNav == 3}" @click="changeNav(3)">我的下级<span></span></a></li>
            </ul>
        </div>

        <!--  以下可以分离成组件  -->
        <!--<reddis-box :currency="this.currency_id" v-show="activeNav == 0"></reddis-box>-->
        <group-box :currency="this.currency_id" v-show="activeNav == 1"></group-box>
        <redlog-box :currency="this.currency_id" v-show="activeNav == 2"></redlog-box>
        <user-box :currency="this.currency_id" v-show="activeNav == 3"></user-box>
    </div>
</template>

<script>
    import { currencyFirst, currencyInfo} from "@/api/index";
    import { redProfit} from "@/api/user";
    import groupBox from "@/page/mine/red/components/group"
    import redlogBox from "@/page/mine/red/components/redlog"
    import reddisBox from "@/page/mine/red/components/reddis"
    import userBox from "@/page/mine/red/components/user"

    export default {
        name: "Redindex",
        components:{groupBox, redlogBox, reddisBox, userBox},
        data()
        {
            return {
                //高亮
                activeNav:1,
                // 币种id
                currency_id:'',
                // 币种信息
                currency:{
                    name:'',
                    tag:'',
                    icon:''
                },
                // 红包总收益
                redProfit:0
            }
        },
        created()
        {
            this.pageInit()
        },
        mounted()
        {



        },
        methods:{

            async pageInit()
            {

                this.currency_id = this.$route.query.currencyId

                /** 处理一下 默认先为1 */
                this.currency_id  =  this.currency_id ?  this.currency_id : 1;
                if (this.currency_id)
                {
                    let res = await currencyInfo(this.currency_id)
                    this.currency = res

                } else
                {
                    let res = await currencyFirst()
                    this.currency_id = res.id
                    this.currency = res
                }

                let res = await redProfit(this.currency_id);
                this.redProfit = res.redProfit



            },

            getInfo()
            {
                console.log('我执行了')
            },
            changeNav(num)
            {
                this.activeNav = num
            }
        }
    }
</script>

<style scoped>
    .app .accountBox {
        background: #24292E;
    }
    .app .accountBox .usdLogo {
        padding-top: 0.25rem;
    }
    .app .accountBox .usdLogo img {
        width: 0.4rem;
        height: 0.4rem;
        display: block;
        margin: auto;
    }
    .app .type {
        color: #fff;
        font-size: 0.15rem;
        margin-top: 0.06rem;
        text-align: center;
    }
    .app .holder {
        padding-top: 0.05rem;
    }
    .app .holder img {
        width: 0.18rem;
        height: 0.14rem;
        display: block;
        margin: auto;
    }
    .app .holderNum {
        text-align: center;
        padding-top: 0.17rem;
    }
    .app .holderNum span {
        font-size: 0.13rem;
        color: #CDCDCF;
        vertical-align: middle;
    }
    .app .holderNum img {
        width: 0.14rem;
        height: 0.14rem;
        vertical-align: middle;
        margin-left: 0.1rem;
    }
    .app .balance {
        font-size: 0.3rem;
        color: #FFF;
        text-align: center;
        margin: 0.1rem 0 0.2rem;
    }
    .app .titleNav {
        padding-bottom: 0.08rem;
    }
    .app .titleNav li {
        width: 33.3%;
        text-align: center;
    }
    .app .titleNav li a {
        display: block;
        font-size: 0.13rem;
        color: #CDCDCF;
        height: 0.26rem;
    }
    .app .titleNav li a span {
        width: 0.12rem;
        height: 0.04rem;
        border-radius: 0.02rem;
        position: absolute;
        left: 50%;
        margin-left: -0.06rem;
        bottom: 0.01rem;
    }
    .app .titleNav li .extension span {
        background: #3ACFFF;
    }
    .app .titleNav li .group span {
        background: #F94824;
    }
    .app .titleNav li .rob span {
        background: #DFBA8E;
    }
    .app .titleNav li .mySub span {
        background: #DF8E8E;
    }
    .app .titleNav li .active span {
        width: 0.4rem;
        margin-left: -0.2rem;
    }
    .app .bodyBox .bodyTitle {
        height: 0.4rem;
        line-height: 0.4rem;
        background: #F8F8F8;
        font-size: 0.14rem;
        color: #050505;
        text-align: center;
        padding: 0 0.15rem;
    }
    .app .bodyBox .bodyNav {
        background: #f8f8f8;
        height: 0.4rem;
        line-height: 0.4rem;
        font-size: 0.13rem;
        color: #515254;
    }
    .app .bodyBox .bodyNav p {
        width: 33.33%;
        text-align: center;
    }
    .app .bodyBox .conList li {
        height: 0.45rem;
        line-height: 0.45rem;
        border-bottom: 0.01rem solid #f5f5f5;
        font-size: 0.12rem;
        color: #666;
        overflow: hidden;
    }
    .app .bodyBox .conList li span {
        width: 33.33%;
        text-align: center;
        display: inline-block;
        float: left;
    }
</style>



